import { ParentProps, JSX, For } from "solid-js";
const columns = [
  {
    title: "属性名",
  },
  {
    title: "说明",
  },
  {
    title: "类型",
  },
  {
    title: "默认值",
  },
  {
    title: "版本",
  },
];
function Table(props: {
  data: {
    property: string;
    des?: string;
    type?: JSX.Element;
    default?: string;
    version?: string;
  }[];
}) {
  return (
    <table
      style={{
        width: "100%",
        "text-align": "left",
        border: "1px solid rgb(5 5 5 / 6%)",
        "border-radius": "4px",
      }}
    >
      <thead>
        <tr>
          <For each={columns}>
            {(column) => (
              <th
                style={{
                  background: "rgb(0 0 0 / 2%)",
                  "padding-block": ".5rem",
                }}
              >
                {column.title}
              </th>
            )}
          </For>
        </tr>
      </thead>
      <tbody>
        <For each={props.data}>
          {(row) => {
            return (
              <tr>
                <td
                  style={{
                    padding: "12px 24px",
                  }}
                >
                  {row.property}
                </td>
                <td
                  style={{
                    padding: "12px 24px",
                  }}
                >
                  {row.des}
                </td>
                <td
                  style={{
                    color: "#c41d7f",
                    padding: "12px 24px",
                  }}
                >
                  {row.type}
                </td>
                <td
                  style={{
                    padding: "12px 24px",
                  }}
                >
                  {row.default || "-"}
                </td>
                <td
                  style={{
                    padding: "12px 24px",
                  }}
                >
                  {row.version}
                </td>
              </tr>
            );
          }}
        </For>
      </tbody>
    </table>
  );
}
export default Table
